/* reset css */
/* https://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

#schedule ol,
#schedule ul {
	list-style: none;
}
#schedule blockquote,
#schedule q {
	quotes: none;
}
#schedule blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
#schedule table {
	border-collapse: collapse;
	border-spacing: 0;
}

/* style css */
/* -------------------------------- 

Primary style

-------------------------------- */
*, *::after, *::before {
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

